.picky {
    width: 100%;
    display: block;

    /*height: 30px;*/
    position: relative;
}
.picky * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}


.picky__input {
    width: 92%;
    background-color: white;
    border: 0px;
    border-width: 1px;
    border-color: #eee;
    border-style: solid;
    border-radius: 2px;
    padding: 5px;
    text-align: left;
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 28px;

}

.picky__input--disabled,
.picky__input--disabled + .picky__dropdown .option {
    color: rgb(95, 94, 94);
}
.picky__input::after {
    position: absolute;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: middle;
    border-top: 4px solid;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    content: '';
    right: 16px;
    top: 16px;
}
.picky__dropdown {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently supported by Chrome and Opera */
    width: 92%;
    position: relative;
    top: 0;
    left: 0;
    z-index: 99;
    background: white;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    /*top: 39px;*/
}


.picky__dropdown .option,
.picky__dropdown li {


    position: relative;
    top: -4px;
    list-style: none;
    background-color: white;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently supported by Chrome and Opera */
    padding: 0.5em;
    cursor: pointer;
    border-bottom: 1px solid #eee;
    text-align: left;
}

.option input:disabled, .option input[readonly], .option textarea:disabled, .option textarea[readonly] { background-color: #e6e6e6; cursor: pointer; }

.picky__filter {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding: 3px;
}
.picky__filter__input {
    width: 100%;
    padding: 3px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: absolute;
    height: 36px;
}



.option input[type="checkbox" i]  {

    display: inline-block;
    white-space: nowrap;
    vertical-align: middle;

    height: 30px ;
    width: 18px ;
    font-size: 40px;


}

