      overflow: hidden;
      -webkit-transition: all 80ms;
      -o-transition: all 80ms;
      transition: all 80ms; }
      .leaflet-sidebar-tabs > li:hover, .leaflet-sidebar-tabs > ul > li:hover {
        color: #000;
